body,html{
  height:100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
.child-view {
  position:absolute;
  width: 100%;
  transition: transfrom .5s cubic-bezier(.55,0,.1,1);
}
/*过场动画*/
@-webkit-keyframes bounceInLeft {
  from {
    opacity: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
  to {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

@keyframes bounceInLeft {

  from{
    opacity: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }

  to {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

.bounceInLeft {
  -webkit-animation: bounceInLeft .5s;
  animation: bounceInLeft .5s;
}

@-webkit-keyframes bounceOutRight {

  from {
    opacity: 1;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

@keyframes bounceOutRight {

  from {
    opacity: 1;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
  }

  to {
     opacity: 0;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

.bounceOutRight {
  -webkit-animation: bounceOutRight .5s;
  animation: bounceOutRight .5s;
}
.slide-left-enter,
.slide-right-enter{
  position:absolute;
  width: 100%;
}

.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
}
.popOut-transition, .popIn-transition {
-webkit-animation-duration: 0.3s;
animation-duration: 0.3s;
-webkit-animation-fill-mode: both;
animation-fill-mode: both;
}

.popOut-enter,
.popOut-leave,
.popIn-enter,
.popIn-leave {
position: absolute;
top: 0;
right: 0;
bottom: 0;
left: 0;
}

.popOut-enter {
z-index: -1;
-webkit-animation-name: popInLeft;
animation-name: popInLeft;
}
.popOut-leave {
-webkit-animation-name: popOutRight;
animation-name: popOutRight;
}

.popIn-enter {
-webkit-animation-name: popInRight;
animation-name: popInRight;
}
.popIn-leave {
-webkit-animation-name: popOutLeft;
animation-name: popOutLeft;
}
@-webkit-keyframes popInLeft {
from {
-webkit-transform: translate3d(-50%, 0, 0);
transform: translate3d(-50%, 0, 0);
visibility: visible;
-webkit-filter: brightness(0.9);
filter: brightness(0.9);
}
to {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
-webkit-filter: brightness(1);
filter: brightness(1);
}
}
@keyframes popInLeft {
from {
-webkit-transform: translate3d(-50%, 0, 0);
transform: translate3d(-50%, 0, 0);
visibility: visible;
-webkit-filter: brightness(0.9);
filter: brightness(0.9);
}
to {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
-webkit-filter: brightness(1);
filter: brightness(1);
}
}
@-webkit-keyframes popOutLeft {
from {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
-webkit-filter: brightness(1);
filter: brightness(1);
}
to {
visibility: hidden;
-webkit-transform: translate3d(-50%, 0, 0);
transform: translate3d(-50%, 0, 0);
-webkit-filter: brightness(0.9);
filter: brightness(0.9);
}
}
@keyframes popOutLeft {
from {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
-webkit-filter: brightness(1);
filter: brightness(1);
}
to {
visibility: hidden;
-webkit-transform: translate3d(-50%, 0, 0);
transform: translate3d(-50%, 0, 0);
-webkit-filter: brightness(0.9);
filter: brightness(0.9);
}
}

@-webkit-keyframes popInRight {
from {
-webkit-transform: translate3d(100%, 0, 0);
transform: translate3d(100%, 0, 0);
visibility: visible;
}
to {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
}
}
@keyframes popInRight {
from {
-webkit-transform: translate3d(100%, 0, 0);
transform: translate3d(100%, 0, 0);
visibility: visible;
}
to {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes popOutRight {
from {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
}
to {
visibility: hidden;
-webkit-transform: translate3d(100%, 0, 0);
transform: translate3d(100%, 0, 0);
}
}
@keyframes popOutRight {
from {
-webkit-transform: translate3d(0, 0, 0);
transform: translate3d(0, 0, 0);
}
to {
visibility: hidden;
-webkit-transform: translate3d(100%, 0, 0);
transform: translate3d(100%, 0, 0);
}
}



